@import "normalize";
@import "fonts";
@import "mixins";

$yellow: #e8c300;

* {
  margin: 0px;
  padding: 0px;
}

a {
  text-decoration: none;
  color: white;
}

body {
  a:hover,
  a:active {
    color: gray;
  }
  color: white;
  font-family: "Open Sans";
  box-sizing: border-box;
}
.container {
  margin: 0 auto;
  padding-left: 80px;
  padding-right: 80px;
}

.btn {
  &:hover {
    background-color: $yellow;
  }
}

@media (min-width: 1400px) {
  .container {
    max-width: 1440px;
  }
}


header {
  background-image: url(../imgs/header-bg.png);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  height: 100%;
  height: 100vh;
  position: relative;
  .menu-list {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-weight: bold;
    font-size: 12px;
    padding-left: 20px;
    padding-right: 20px;
    li {
      list-style-type: none;
      height: 100px;
      a {
        text-transform: uppercase;
      }
      &:nth-child(1) {
        &::after {
          content: "";
          position: absolute;
          display: block;
          width: 35%;
          height: 1px;
          border-bottom: 1px solid rgba(255, 255, 255, 0.2);
          top: 100px;
          left: 60px;
        }
      }
      &:nth-child(5) {
        &::after {
          content: "";
          position: absolute;
          display: block;
          width: 35%;
          height: 1px;
          border-bottom: 1px solid rgba(255, 255, 255, 0.2);
          top: 100px;
          right: 60px;
        }
      }
    }

    .logo {
      margin-left: 35px;
      margin-right: 35px;
      margin-top: 25px;
      height: auto;
    }
  }

  .hamburger-menu {
    display: none;
    .hamburger-button {
      width: 35px;
      height: 35px;
      background-color: transparent;
      border: none;
      position: fixed;
      left: 10px;
      top: 40px;
      z-index: 999;
      overflow: hidden;
      outline: none;
      transition: 0.4s;
      span {
        display: block;
        position: absolute;
        background-color: $yellow;
        height: 5px;
        width: 35px;
        top: 15px;
        &::after,
        &::before {
          display: block;
          position: absolute;
          background-color: $yellow;
          height: 5px;
          width: 35px;
          content: "";
        }
        &::after {
          bottom: 10px;
        }
        &::before {
          top: 10px;
        }
      }
    }
    .hamburger-button-line {
      span {
        &::after {
          transition: transform 0.2s ease-in, bottom 0.2s linear 0.2s;
        }
        &::before {
          transition: transform 0.2s ease-in, top 0.2s linear 0.2s;
        }
      }
    }
    
    .hamburger-list {
      width: 30%;
      height: 100%;
      position: fixed;
      background-color: #0d0d0d;
      opacity: 0.9;
      z-index: 998;
      padding-top: 30px;
      padding-left: 40px;
      top: 0;
      left: 0;
      transition: 0.5s;
      overflow: auto;
      display: none;
      li {
        list-style-type: none;
        padding: 20px 10px;
        border-bottom: 1px solid #afafaf;
        margin-left: 10%;
        margin-right: 50px;
        a {
          font-size: 18px;
          color: #ffffff;
          font-family: "Open Sans";
          text-transform: uppercase;
          font-weight: 700;
        }
        &:hover {
          text-decoration: underline;
          filter: brightness(0.7);
        }
      }
    }
    .hamburger-overlay {
      display: none;
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: 100vh;
      z-index: 997;
    }
    &_active {
      .hamburger-button-line {
        span {
          width: 0;
          &::after,
          &::before {
            transition: top 0.2s linear, transform 0.2s ease-in 0.2s;
          }
          &::after {
            bottom: 0;
            transform: rotate(45deg);
          }
          &::before {
            top: 0;
            transform: rotate(-45deg);
          }
        }
      }
      .hamburger-overlay {
        display: block;
      }
      .hamburger-list {
        display: block;
      }
    }
  }

  .main-text-block {
    display: flex;
    justify-content: center;
    .main-text {
      text-align: center;
      position: relative;
      padding-top: 150px;
      p {
        font-weight: bold;
        font-size: 12px;
        letter-spacing: 0.5em;
        text-transform: uppercase;
        padding-top: 50px;
      }
      h1 {
        font-family: Banny;
        font-size: 96px;
        text-transform: uppercase;
        margin-top: 0;
        margin-bottom: 0;
        &::after {
          @include yellow-line;
        }
      }
      .button-block {
        text-transform: uppercase;
        display: flex;
        justify-content: center;
        .button {
          display: flex;
          justify-content: center;
          align-items: center;
          margin-top: 36px;
          margin-bottom: 80px;
          border: 1px solid rgba(255, 255, 255, 0.5);
          border-radius: 5px;
          width: 200px;
          min-height: 60px;
          &:first-child {
            margin-right: 40px;
          }
          a {
            font-weight: bold;
            font-size: 12px;
          }
          &:hover {
            background-color: $yellow;
          }
        }
      }
      .down-btn {
        display: inline-block;
        font-weight: 900;
        font-size: 20px;
        color: white;
        background-color: transparent;
        border: 1px solid rgba(255, 255, 255, 0.2);
        border-radius: 50%;
        padding: 5px;
        margin-bottom: 90px;
        outline: none;
        width: 35px;
        height: 35px;
      }
    }
  }
  .schedule {
    position: absolute;
    font-weight: bold;
    font-size: 12px;
    transform: rotate(-90deg);
    left: -80px;
    bottom: 290px;
  }
  .social-block {
    display: flex;
    flex-direction: column;
    position: absolute;
    right: 70px;
    bottom: 190px;
    a {
      display: flex;
      width: 40px;
      height: 40px;
      border: 1px solid rgba(255, 255, 255, 0.2);
      border-radius: 50%;
      margin-bottom: 30px;
      align-items: center;
      justify-content: center;
      i {
        font-size: 20px;
      }
    }
  }
}

.about-us {
  color: black;
  .title {
    @include title;
  }
  .wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 40px;
    .text-block {
      @include text-block;
      width: 85%;
      h2 {
        @include h2;
        &::after {
          @include yellow-line;
        }
      }
      h3 {
        @include h3;
      }
      .text {
        font-size: 15px;
      }
    }
    .image-block {
      position: relative;
      margin-top: 40px;
      width: 100%;
      .square {
        @include square;
        left: 395px;
        top: 163px;
      }
      img {
        max-width: 575px;
      }
    }
  }
}

.our-team {
  background-image: url(../imgs/team-bg.png);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  background-attachment: fixed;
  .title {
    @include title;
  }
  .wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    .image-block {
      position: relative;
      margin-bottom: 40px;
      width: 100%;
      img {
        margin-top: -50px;
        margin-left: 80px;
        max-width: 575px;
      }
      .square {
        @include square;
      }
    }
    .text-block {
      @include text-block;
      padding-left: 0;
      padding-bottom: 100px;
      width: 100%;
      h2 {
        @include h2;
        &::after {
          @include yellow-line;
          margin-left: -30px;
        }
      }
      h3 {
        @include h3;
      }
      .text {
        font-size: 15px;
      }
    }
  }
}

.booking {
  color: black;
  .title {
    @include title;
  }
  .wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    .fields-block {
      @include text-block;
      padding-left: 0;
      width: 100%;
      h2 {
        @include h2;
        &::after {
          @include yellow-line;
        }
      }
      .fields {
        display: flex;
        flex-wrap: wrap;
        box-sizing: border-box;
        position: relative;
        padding-top: 10px;
        p {
          width: 50%;
          position: relative;
          input,
          select {
            border: none;
            background: #e5e5e5;
            border-radius: 5px;
            width: 96%;
            height: 60px;
            font-size: 14px;
            color: rgba(51, 51, 51, 0.7);
            outline: none;
            box-sizing: border-box;
            margin: 2%;
            padding: 22px 20px;
            text-align: left;
          }
          select {
            position: relative;
            appearance: none;
            option {
              margin-bottom: 20px;
            }
          }
        }
        button {
          color: white;
          background-color: $yellow;
          width: 98%;
          height: 60px;
          display: flex;
          justify-content: center;
          align-items: center;
          margin: 1%;
          border-radius: 5px;
          border: none;
          padding-left: 20px;
          padding-right: 20px;
        }
      }
    }
    .image-block {
      position: relative;
      margin-top: 40px;
      width: 100%;
      .square {
        @include square;
        left: 395px;
        top: 163px;
      }
      img {
        max-width: 575px;
      }
    }
  }
  .contacts {
    font-size: 12px;
    color: rgba(51, 51, 51, 0.5);
    text-align: center;
    padding-bottom: 45px;
  }
}

.specialties {
  background-image: url(../imgs/events-bg.png);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  background-attachment: fixed;
  .title {
    @include title;
  }
  .wrapper {
    position: relative;
    .item {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding-bottom: 40px;
      .image-block {
        position: relative;
        width: 50%;
        img {
          margin-top: -50px;
          margin-left: 80px;
          max-width: 575px;
        }
        .square {
          @include square;
        }
      }
      .text-block {
        width: 50%;
        padding-left: 50px;
        padding-bottom: 100px;
        @include text-block;
        h2 {
          @include h2;
          &::after {
            @include yellow-line;
          }
        }
        h3 {
          @include h3;
        }
        .text {
          font-size: 15px;
        }
      }
    }
    .owl-dots {
      display: flex;
      position: absolute;
      margin-top: 0;
      bottom: 20px;
      left: 50%;
      margin-left: -36px;
      margin-bottom: 20px;
    }
  }
}

.menu {
  color: black;
  .text-block {
    @include text-block;
    h2 {
      @include h2;
      padding-top: 80px;
      &::after {
        @include yellow-line;
      }
    }
    h3 {
      @include h3;
      padding-left: 26%;
      padding-right: 26%;
      padding-bottom: 0;
    }
    .tabs {
      .menu-list {
        display: flex;
        justify-content: center;
        align-items: center;
        padding-top: 50px;
        padding-bottom: 50px;
        li {
          list-style-type: none;
          border-right: 1px solid rgba(51, 51, 51, 0.2);
          &:last-child {
            border-right: none;
          }
          a {
            color: black;
            font-weight: bold;
            font-size: 12px;
            padding-left: 45px;
            padding-right: 45px;
            text-transform: uppercase;
            outline: none;
          }
        }
      }
      .menu-items {
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex-wrap: wrap;
        padding-bottom: 82px;
        .menu-item {
          text-align: left;
          display: flex;
          flex-direction: column;
          padding-bottom: 18px;
          width: 30%;
          .menu-item-name,
          .span {
            font-family: Banny;
            font-size: 18px;
            text-transform: uppercase;
            color: #333333;
          }
          .menu-item-description {
            line-height: 0.5;
            font-size: 12px;
            color: rgba(51, 51, 51, 0.5);
          }
        }
      }
    }
    .ui-widget-content {
      border: none;
    }
    .ui-widget-header {
      border: none;
      background: transparent;
    }
    .ui-state-default {
      border: none;
      background: transparent;
      outline: none;
    }
    .ui-state-active {
      background-color: #f6f6f6;
      outline: none;
    }
  }
}

.events {
  background-image: url(../imgs/events-bg.png);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  background-attachment: fixed;
  .title {
    @include title;
  }
  .wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    .image-block,
    .image-block-right {
      position: relative;
      .text-rotate-top,
      .text-rotate-bottom {
        font-family: Banny;
        font-size: 24px;
        text-transform: uppercase;
        position: absolute;
        background-color: black;
        width: 437px;
        padding-top: 27px;
        padding-bottom: 27px;
        padding-left: 20px;
        padding-right: 20px;
      }
    }
    .image-block {
      img {
        margin-top: -50px;
        margin-left: 20px;
        max-width: 575px;
      }
      .square {
        @include square;
        left: -55px;
      }
      .text-rotate-top {
        transform: rotate(90deg);
        right: -150px;
        top: 197px;
      }
    }
    .image-block-right {
      img {
        margin-top: -50px;
        max-width: 575px;
      }
      .square {
        @include square;
        left: auto;
        right: -75px;
      }
      .text-rotate-bottom {
        transform: rotate(-90deg);
        bottom: 250px;
        left: -150px;
      }
    }
  }
  .tel {
    font-size: 12px;
    color: rgba(255, 255, 255, 0.5);
    text-align: center;
    padding-bottom: 45px;
  }
}

.gallery {
  .container {
    padding: 0;
    .gallery-images {
      display: flex;
      margin-top: -1px;
      img {
        max-width: 25%;
      }
    }
  }
}

.contact {
  color: black;
  .text-block {
    text-align: center;
    h2 {
      @include h2;
      padding-top: 80px;
      &::after {
        @include yellow-line;
      }
    }
    h3 {
      @include h3;
      padding-left: 26%;
      padding-right: 26%;
      padding-bottom: 0;
    }
  }
  .contact-form {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    box-sizing: border-box;
    position: relative;
    padding-top: 10px;
    padding-bottom: 100px;
    p {
      width: 32%;
      position: relative;
      input,
      textarea {
        border: none;
        background: #e5e5e5;
        border-radius: 5px;
        height: 60px;
        font-size: 14px;
        color: rgba(51, 51, 51, 0.7);
        outline: none;
        box-sizing: border-box;
        padding: 22px 20px;
        text-align: left;
        width: 100%;
      }
      textarea {
        width: 100%;
        margin-top: 10px;
        margin-bottom: 10px;
        height: 120px;
      }
    }
    .textarea {
      width: 100%;
    }
    .email {
      padding-left: 2%;
      padding-right: 2%;
    }
    .contacts-block {
      display: flex;
      justify-content: flex-start;
      width: 48%;
      p {
        width: auto;
        text-align: left;
        font-size: 12px;
        color: rgba(51, 51, 51, 0.5);
        &:nth-child(2) {
          padding-left: 2%;
          padding-right: 2%;
        }
        i {
          color: $yellow;
          padding-right: 5px;
        }
      }
    }
    button {
      color: white;
      background-color: $yellow;
      width: 50%;
      height: 60px;
      display: flex;
      justify-content: center;
      align-items: center;
      border-radius: 5px;
      border: none;
      padding-left: 20px;
      padding-right: 20px;
    }
  }
}

.map {
  height: 475px;
  iframe {
    width: 100%;
    height: 475px;
    border: 0;
  }
}

footer {
  background-color: rgba(0, 0, 0, 0.9);
  text-align: center;
  p {
    font-size: 12px;
    color: rgba(255, 255, 255, 0.5);
    padding-top: 43px;
    padding-bottom: 43px;
  }
}



@import "media";