@mixin yellow-line {
    content: "";
    display: block;
    width: 60px;
    height: 5px;
    background-color: $yellow;
    position: absolute;
    left: 50%;
    margin-left: -30px;
}

@mixin h2 {
    font-family: Banny;
    font-size: 36px;
    text-transform: uppercase;
    padding-top: 7px;
    padding-bottom: 7px;
}

@mixin h3 {
    font-weight: bold;
    font-size: 18px;
    padding-top: 7px;
    padding-bottom: 28px;
}

@mixin title {
    font-weight: bold;
    font-size: 12px;
    letter-spacing: 0.5em;
    text-transform: uppercase;
    text-align: center;
    padding-top: 44px;
    padding-bottom: 44px;
}

@mixin text-block {
    text-align: center;
    position: relative;
    line-height: 1.5;
}

@mixin square {
    position: absolute;
    width: 210px;
    height: 210px;
    left: 0px;
    top: 113px;
    border: 20px solid #E8C300;
    box-shadow: 0px 0px 100px #000000;
    border-radius: 5px;
}