@media only screen and (max-height: 1050px) {
  header {
    .schedule {
      bottom: 48%;
    }
    .social-block {
      bottom: 29%;
    }
  }
}

@media only screen and (max-height: 800px) {
  header {
    .main-text-block {
      .main-text {
        padding-top: 5%;
      }
    }
  }
}

@media only screen and (max-height: 650px) {
  header {
    height: 100%;
    .main-text-block {
      .main-text {
        padding-top: 2%;
      }
    }
  }
}

@media only screen and (min-height: 1050px) {
  header {
    .main-text-block {
      .main-text {
        h1 {
          padding-top: 100px;
          padding-bottom: 100px;
          &::after {
            margin-top: 40px;
          }
        }
      }
    }
    .schedule {
      bottom: 48%;
    }
    .social-block {
      bottom: 39%;
    }
  }
}


@media only screen and (max-width: 1400px) {
  .about-us,
  .our-team,
  .booking {
    .wrapper {
      .image-block {
        img {
          max-width: 475px;
        }
      }
    }
  }
  .about-us {
    .wrapper {
      .text-block {
        width: 100%;
      }
      .image-block {
        .square {
          left: 310px;
          top: 120px;
        }
      }
    }
  }
  .our-team {
    .wrapper {
      .image-block {
        .square {
          top: 70px;
        }
      }
      .text-block {
        padding-bottom: 70px;
      }
    }
  }
  .booking {
    .wrapper {
      .image-block {
        .square {
          left: 310px;
          top: 120px;
        }
      }
    }
  }
  .specialties {
    .wrapper {
      .item {
        .text-block {
          padding-bottom: 70px;
        }
        .image-block {
          img {
