            top: 70px;
          }
        }
      }
    }
  }
  .menu {
    .text-block {
      h3 {
        padding-left: 20%;
        padding-right: 20%;
      }
    }
  }
  .events {
    .wrapper {
      .image-block,
      .image-block-right {
        img {
          max-width: 475px;
        }
