          top: 70px;
        }
      }
      .image-block {
        .text-rotate-top {
          width: 72%;
          right: -120px;
          top: 147px;
        }
      }
      .image-block-right {
        .text-rotate-bottom {
          width: 75%;
          left: -120px;
          bottom: 200px;
        }
      }
    }
  }
}

@media only screen and (max-width: 1200px) {
  header {
    .main-text-block {
      .main-text {
        h1 {
          font-size: 76px;
        }
      }
    }
  }
  .about-us,
  .our-team,
  .booking {
    .wrapper {
      .image-block {
        width: 50%;
        img {
          max-width: 400px;
        }
        .square {
          width: 150px;
          height: 150px;
          border: 15px solid #e8c300;
        }
      }
    }
  }
  .about-us,
  .booking {
    .wrapper {
      .image-block {
        .square {
          left: 270px;
          top: 110px;
        }
      }
    }
  }
  .our-team {
    .title {
      padding-bottom: 20px;
    }
    .wrapper {
      .image-block {
        img {
          margin-top: 0;
          margin-left: 0;
        }
        .square {
          left: -50px;
          top: 110px;
        }
      }
      .text-block {
        padding-bottom: 0;
      }
    }
  }
  .specialties {
    .title {
      padding-bottom: 20px;
    }
    .wrapper {
      .item {
        .image-block {
          .square {
            width: 150px;
            height: 150px;
            border: 15px solid #e8c300;
            left: 0px;
            top: 110px;
          }
          img {
            margin-top: 0px;
            margin-left: 50px;
            max-width: 400px;
          }
        }
      }
    }
  }
  .menu .text-block .tabs .menu-items .menu-item {
    width: auto;
    padding-right: 10px;
  }
  .events {
    .wrapper {
      .image-block,
      .image-block-right {
        img {
          max-width: 400px;
          margin-top: -40px;
        }
        .square {
          width: 150px;
          height: 150px;
          border: 15px solid #e8c300;
        }
      }
      .image-block {
        img {
          margin-left: 0;
        }
        .text-rotate-top {
          width: 74%;
          right: -94px;
          top: 120px;
        }
      }
      .image-block-right {
        .square {
          left: 270px;
        }
        .text-rotate-bottom {
          width: 74%;
          left: -95px;
          bottom: 164px;
        }
      }
    }
  }
  .contact {
    .text-block {
      h3 {
        padding-left: 15%;
        padding-right: 15%;
      }
    }
  }
}

@media only screen and (max-width: 992px) {
  .container {
    padding-left: 10px;
    padding-right: 10px;
  }
  .about-us,
  .our-team,
  .booking {
    .wrapper {
      .image-block {
        .square {
          width: 100px;
          height: 100px;
          left: 0px;
          border: 10px solid #e8c300;
          right: auto;
          top: 30%;
        }
      }
    }
  }
  header {
    .menu-list {
      li {
        display: none;
      }
      .logo {
        display: block;
        margin-left: auto;
        margin-right: auto;
        padding-left: 0px;
        padding-right: 0px;
        a {
          img {
            max-width: 80%;
            margin-left: 10%;
          }
        }
        &:nth-child(1) {
          &::after {
            display: none;
          }
        }
        &:nth-child(5) {
          &::after {
            display: none;
          }
        }
      }
    }
    .hamburger-menu {
      display: block;
    }
    .main-text-block {
      .main-text {
        h1 {
          font-size: 70px;
        }
        .down-btn {
          margin-top: 50px;
        }
      }
    }
    .schedule {
      position: relative;
      transform: none;
      left: 0px;
      bottom: 220px;
      text-align: center;
    }
    .social-block {
      position: relative;
      flex-direction: row;
      align-items: center;
      justify-content: center;
      right: 0px;
      bottom: 230px;
      a {
        margin-right: 10px;
        margin-left: 10px;
        margin-top: 30px;
      }
    }
  }

  .about-us {
    .wrapper {
      justify-content: space-around;
      margin-top: 40px;
      .text-block {
        width: 50%;
        h2 {
          font-size: 26px;
        }
      }
      .image-block {
        margin-top: 0;
        max-width: 300px;
        img {
          max-width: 100%;
        }
      }
    }
  }

  .our-team {
    .wrapper {
      justify-content: space-around;
      .text-block {
        width: 50%;
        h2 {
          font-size: 26px;
        }
      }
      .image-block {
        margin-bottom: 0;
        max-width: 300px;
        img {
          max-width: 100%;
          margin-top: -30px;
          margin-left: 0;
        }
        .square {
          margin-top: -15px;
        }
      }
    }
  }

  .booking {
    .wrapper {
      justify-content: space-around;
      .fields-block {
        max-width: 50%;
        h2 {
          padding-top: 30px;
          font-size: 26px;
        }
        .fields {
          p {
            width: 100%;
            input,
            select {
              width: 100%;
              margin: 5px 0;
            }
          }
          button {
            width: 100%;
            margin: 5px 0;
          }
        }
      }
      .image-block {
        margin-top: 0;
        width: 300px;
        img {
          max-width: 100%;
        }
      }
    }
  }

  .specialties {
    .title {
      padding-bottom: 10px;
    }
    .wrapper {
      .item {
        padding-bottom: 100px;
        justify-content: space-around;
        .image-block {
          width: 300px;
          margin-bottom: 0;
          img {
            max-width: 100%;
            margin-top: 0px;
            margin-left: 0px;
          }
          .square {
            width: 100px;
            height: 100px;
            left: 0px;
            border: 10px solid #e8c300;
            right: auto;
            top: 30%;
          }
        }
        .text-block {
          width: 50%;
          padding-left: 0;
          padding-bottom: 0;
          h2 {
            font-size: 26px;
          }
        }
      }
      .owl-dots {
        margin-top: 30px;
        bottom: 30px;
      }
    }
  }

  .menu {
    .text-block {
      h2 {
        padding-top: 30px;
        font-size: 26px;
      }
      h3 {
        padding-left: 10%;
        padding-right: 10%;
      }
      .tabs {
        .menu-list {
          flex-wrap: wrap;
          padding-top: 30px;
          padding-bottom: 30px;
          li {
            border-right: 0;
            a {
              padding-left: 10px;
              padding-right: 10px;
            }
          }
        }
        .menu-items {
          padding: 0;
          padding-bottom: 40px;
          justify-content: space-around;
          .menu-item {
            width: auto;
            .menu-item-name {
              font-size: 14px;
            }
          }
        }
      }
    }
  }

  .events {
    .title {
      padding-top: 30px;
      padding-bottom: 30px;
    }
    .wrapper {
      justify-content: space-around;
      .image-block,
      .image-block-right {
        img {
          max-width: 300px;
          margin-top: -30px;
        }
        .square {
          width: 100px;
          height: 100px;
          left: 0px;
          border: 10px solid #e8c300;
          right: auto;
          top: 20%;
        }
      }
      .image-block {
        img {
          margin-left: 0;
        }
        .text-rotate-top {
          width: 70%;
          right: -75px;
          top: 82px;
          font-size: 20px;
        }
      }
      .image-block-right {
        .text-rotate-bottom {
          width: 70%;
          left: auto;
          right: -75px;
          bottom: 115px;
          font-size: 20px;
        }
      }
    }
  }
  .contact {
    .contact-form {
      .contacts-block {
        flex-direction: column;
        p {
          &:nth-child(2) {
            padding: 10px 0;
          }
        }
      }
    }
  }
}

@media only screen and (max-width: 768px) {
  .wrapper {
    padding-left: 50px;
    padding-right: 50px;
  }
  header {
    .hamburger-menu {
      .hamburger-list {
        width: 50%;
      }
    }
    .main-text-block {
      .main-text {
        h1 {
          font-size: 60px;
        }
      }
    }
  }
  .about-us {
    .wrapper {
      flex-direction: column;
      justify-content: center;
      .text-block {
        width: 100%;
      }
      .image-block {
        width: 100%;
      }
    }
  }

  .our-team {
    .wrapper {
      flex-direction: column;
      .text-block {
        width: 100%;
        padding-bottom: 40px;
      }
      .image-block {
        width: 100%;
      }
    }
  }

  .booking {
    .wrapper {
      flex-direction: column;
      .fields-block {
        max-width: 100%;
        .fields {
          align-content: space-around;
          p {
            width: 50%;
            input, select {
              width: 98%;
            }
          }
          button {
            width: 99%;
            margin: 5px auto;
        }
        }
      }
      .image-block {
        max-width: 300px;
        width: 100%;
      }
    }
  }

  .specialties {
    .title {
      padding-bottom: 10px;
    }
    .wrapper {
      padding-left: 0;
      padding-right: 0;
      .item {
        width: 100%;
        display: flex;
        flex-direction: column;
        
        .text-block {
          width: 90%;
          padding-left: 50px;
      padding-right: 50px;
        }
      }
    }
  }

  .menu {
    .text-block {
      h3 {
        padding-left: 0;
        padding-right: 0;
      }
      .tabs {
        padding-bottom: 40px;
        .menu-items {
          justify-content: center;
          max-height: 200px;
          overflow: scroll;
        }
      }
    }
  }

  .events {
    .wrapper {
      flex-direction: column;
      justify-content: center;
      .image-block-right {
        .text-rotate-bottom {
          transform: rotate(90deg);
        }
      }
    }
  }

  .gallery {
    .gallery-images {
      flex-wrap: wrap;
      justify-content: center;
      img {
        max-width: 50% !important;
        margin: 10px;
      }
    }
  }

  footer {
    p {
      padding-top: 30px;
      padding-bottom: 30px;
    }
  }
}

@media only screen and (max-width: 520px) {
  .wrapper {
    padding-left: 0px;
    padding-right: 0px;
  }
  header {
    .hamburger-menu {
      .hamburger-list {
        width: 100%;
      }
    }
    .main-text-block {
      .main-text {
        h1 {
          font-size: 30px;
          padding-top: 20px;
          padding-bottom: 10px;
          &::after {
            margin-top: 10px;
          }
        }
        .button-block {
          flex-direction: column;
          align-items: center;
          justify-content: center;
          .button {
            margin-top: 30px;
            margin-bottom: 0;
            min-height: 40px;
            &:first-child {
              margin-right: 0;
            }
            &:last-child {
              margin-bottom: 30px;
            }
          }
        }
        .down-btn {
          display: none;
        }
        p {
          padding-top: 30px;
        }
      }
      
    }
    .schedule {
      bottom: 0;
    }
    .social-block {
      bottom: 0;
    }
  }
  .booking {
    .wrapper {
      .fields-block {
        .fields {
          p {
            width: 100%;
          }
        }
      }
    }
  }
  .contact {
    .contact-form {
      .email {
        padding: 10px 0;
      }
      p {
        width: 100%;
      }
    }
  }
  .gallery {
      .gallery-images {
      img {
        margin: 0;
      }
    }
  }
}


